@import url(https://fonts.googleapis.com/css2?family=Merriweather&family=Nanum+Gothic&family=Nunito&family=Open+Sans&family=Parisienne&family=Space+Mono&display=swap);
*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 10px;
  height: 8px;
}
::-webkit-scrollbar-button {
  width: 0px;
  height: 0px;
}
::-webkit-scrollbar-thumb {
  background: rgba(var(--white), 0.2);
  border: 0px none #ffffff;
  border-radius: 50px;
}
::-webkit-scrollbar-thumb:hover {
  background: white;
  border: 0px none #ffffff;
  border-radius: 50px;
}

::-webkit-scrollbar-thumb:active {
  background: white;
}
::-webkit-scrollbar-track {
  background: rgb(var(--backgrColor));
  border: 2px none #ffffff;
  border-radius: 50px;
}

::-webkit-scrollbar-corner {
  background: transparent;
}
body {
  position: relative;
  margin: 0;
  color: white;
  background-color: rgb(var(--backgrColor));
}
a {
  color: white;
  text-decoration: none;
}
ul {
  list-style-type: none;
}
.container {
  position: relative;
}
button {
  color: white;
  cursor: pointer;
}

#title {
  font-size: 26px;
  width: 100%;
  padding: 0 5px 10px;
}

#title span {
  color: rgb(var(--colorSecondary));
  padding-right: 5px;
}
main {
  opacity: 0;
  transition: opacity 2.8s ease-out;
}
.hightLight {
  color: rgb(var(--colorPrimary));
  font-family: monospace;
}
.icon {
  font-size: 18px;
  vertical-align: middle;
  margin-right: 5px;
}
* {
  transition: opacity 0.5s ease;
}

@media screen and (min-width: 800px) {
  .container {
    margin: 0;
    padding: 0;
  }

  #title {
    font-size: 36px;
    margin: 0 0 20px;
  }
}

.loader-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 1;
  transition: opacity 1s ease;
}
.loader {
  display: block;
  max-height: 280px;
  border-radius: 100%;
}
.loaderBg {
  fill: none;
  stroke: black;
  stroke-width: 4.1;
}
.loaderStroke {
  fill: none;
  stroke-width: 3;
  stroke-linecap: round;
  stroke-dasharray: 0, 100;
  -webkit-animation: load 3s linear forwards;
          animation: load 3s linear forwards;
}

@-webkit-keyframes load {
  to {
    stroke-dasharray: 100, 100;
  }
}

@keyframes load {
  to {
    stroke-dasharray: 100, 100;
  }
}

.about-container {
  width: var(--mobileWidth);
  margin: auto;
  padding: 30px 0 30px 0;
}

.about {
  font-size: 20px;
}

.about-content {
  padding: 0 10px 0 10px;
}

.about p {
  font-size: 18px;
  line-height: 22px;
  color: rgba(var(--white), 0.7);
  font-family: "Nanum Gothic";
  padding: 0 5px 0px 5px;
}

.profilepic {
  position: relative;
  margin: 50px auto;
  width: 12.5em;
  padding: 10px;
}

.profilepic::before,
.profilepic::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  border: 3px solid rgb(var(--colorSecondary));
  border-radius: 10px;
}

.profilepic::before {
  top: -12px;
  left: -10px;
}
.profilepic::after {
  bottom: -12px;
  right: -10px;
}

.profilepic img {
  display: block;
  max-width: 100%;
  border: 1px dashed rgb(var(--colorPrimary));
  border-radius: 10px;
}
.profileIcon {
  width: 5.5em;
  position: absolute;
  bottom: -10px;
  right: -20px;
}
.profileIcon img {
  border: none;
}

@media screen and (min-width: 800px) {
  .about-container {
    height: 88vh;
    width: var(--monitorWidth);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
  }

  .about {
    width: 100%;
  }

  .about-content {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .about-p {
    width: 35em;
  }
  .about p {
    font-size: 24px;
    line-height: 32px;
  }

  .profilepic {
    position: relative;
    margin: 35px;
    width: 16em;
    padding: 12px;
  }
  .profilepic::before,
  .profilepic::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    z-index: -1;
    border: 3px solid rgb(var(--colorSecondary));
    border-radius: 10px;
    transition: all 0.5s ease;
  }
  .profilepic::before {
    top: -20px;
    left: -18px;
  }
  .profilepic::after {
    bottom: -20px;
    right: -18px;
  }

  .profilepic img:hover {
    -webkit-transform: scale(1.065);
            transform: scale(1.065);
    transition: 0.5s ease 0s;
  }
  .profilepic:hover::after {
    bottom: 20px;
    right: 18px;
  }
  .profilepic:hover::before {
    top: 20px;
    left: 18px;
  }
}

.greet-container {
