}

button.burger {
  display: flex;
  grid-gap: 0;
  gap: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  cursor: pointer;
  background-color: transparent;
  border: 0px;
  z-index: 10;
}

.bur1,
.bur2,
.bur3 {
  width: 35px;
  height: 5px;
  margin: 3px 12px;
  background-color: white;
  border-radius: 5px;
  transition: 0.5s;
}

.change.bur1 {
  -webkit-transform: rotate(-45deg) translate(-8px, 9px);
