  --colorPrimary: 18, 111, 252;
  --colorSecondary: 33, 114, 235;
  --white: 255, 255, 255;
  --gradient-1: #7430ce;
  --gradient-2: #117ed3;
  --logo: #357aef;
  --monitorWidth: calc(100vw * 0.75);
